    text-align: right; }

.modal-cart__buttons {
  padding-left: 35px;
  padding-right: 35px;
  display: flex;
  justify-content: space-between; }

.modal-cart__buttons a {
  padding-top: 28px;
  padding-bottom: 28px;
  background-color: #a87b62;
  color: #ffffff;
  text-align: center;
  border: none;
  width: calc((100% - 30px) / 2);
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  border: 1px solid #a87b62; }
  .modal-cart__buttons a:hover {
